import { Router } from 'express';
import { SongController } from '@/controllers/songs.controller';
// import { CreateUserDto } from '@dtos/users.dto';
import { Routes } from '@interfaces/routes.interface';
// import { ValidationMiddleware } from '@middlewares/validation.middleware';

export class SongRoute implements Routes {
  public path = '/songs';
  public router = Router();
  public song = new SongController();

  constructor() {
    this.initializeRoutes();
  }

  private initializeRoutes() {
    this.router.get(`${this.path}/:id`, this.song.getSongById);
    // this.router.post(`${this.path}`, ValidationMiddleware(CreateUserDto), this.song.createUser);
  }
}
